import axios from "axios";
import React, { Component } from "react";
import { MdKeyboardBackspace, MdRadioButtonChecked } from "react-icons/md";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Loading from "../../components/Loading/Loading";
import { APP_URL } from "../../config/app.config";
import { errorToText } from "../../utils/errors";
import { search } from "../../utils/functions";
import { TransferRequestInterface } from "./TransferApproval";

enum TRANSFER_REQUEST_STATUS {
  PENDING = "PENDING",
  APPROVED = "APPROVED",
  REJECTED = "REJECTED",
  CANCELED = "CANCELED",
}

export interface TransferRequestsListInterface {
  teacher_transfer_id: string;
  teacher_reason_id: string | null;
  transfer_type: string;
  teacher_id: string;
  requested_status: TRANSFER_REQUEST_STATUS;
  teacher_supporting_document: string | null;
  approved_school_id: string | null;
  teacher_requested_transfer_date: string;
  school_from_id: string;
  requested_school_id: string;
  teacher_comment: string;
  incoming_decision_date: string | null;
  teacher_full_name: string;
  teacher_first_name: string | null;
  teacher_last_name: string | null;
  teacher_phone_number: string;
  qualification_id: string;
  position_code: string;
  qualification_name: string;
  position_name: string;
  school_from_info: {
    school_name: string;
    school_code: string;
    sector_name: string;
    district_name: string;
  } | null;
  requested_school_info: {
    school_name: string;
    school_code: string;
    sector_name: string;
    district_name: string;
  } | null;
  approved_school_info: {
    school_name: string;
    school_code: string;
    sector_name: string;
    district_name: string;
  } | null;
}

interface NewFormat {}

interface TransferRequestDetailsProps {
  transferDetails: TransferRequestInterface;
  backToApproval: () => void;
}

interface TransferRequestDetailsState {
  loading: boolean;
  searchData: string;
  data: TransferRequestsListInterface[] | null;
}

export class TransferRequestDetails extends Component<
  TransferRequestDetailsProps,
  TransferRequestDetailsState
> {
  constructor(props: TransferRequestDetailsProps) {
    super(props);

    this.state = {
      loading: false,
      searchData: "",
      data: null,
    };
  }

  GetTransferRequestsDetailsList = async (district_code: string) => {
    this.setState({ loading: true });
    try {
      const res = await axios.get<TransferRequestsListInterface[]>(
        `${APP_URL}/teachertransfer/districtDetails/${district_code}`
      );
      if (res.status === 200) {
        this.setState({
          data: res.data,
          loading: false,
        });
        console.log("Res: ", res.data);
      }
    } catch (error: any) {
      console.log("Error: ", { ...error });
      alert(errorToText(error));
      this.setState({
        data: [],
        loading: false,
      });
    }
  };

  componentDidMount = () => {
    this.GetTransferRequestsDetailsList(
      this.props.transferDetails.district_code
    );
  };

  render() {
    return this.state.data === null || this.state.loading === true ? (
      <DashboardContainer>
        <div className="flex flex-col items-center justify-center p-3">
          <Loading />
          <div className="">Loading, please wait...</div>
        </div>
      </DashboardContainer>
    ) : (
      <DashboardContainer>
        <div>
          <div className="flex flex-row items-center gap-3">
            <div
              onClick={this.props.backToApproval}
              className="flex flex-row items-center gap-2 bg-blue-100 text-primary-800 hover:text-white hover:bg-primary-800 rounded cursor-pointer px-3 py-2 font-bold"
            >
              <div>
                <MdKeyboardBackspace className="text-2xl" />
              </div>
              <span>Back to list</span>
            </div>
            <div>
              <div className="text-2xl font-light -mb-1 text-gray-700">
                Transfer requests details
              </div>
              <div>
                <div className="font-extrabold text-black text-lg ml-1 flex items-center gap-2">
                  <div>
                    <div className="">
                      <MdRadioButtonChecked className="text-2xl" />
                    </div>
                  </div>
                  <span>
                    {this.props.transferDetails.district_name} district
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div>
          <table className="w-full mt-5">
            <thead className="bg-blue-100 text-primary-800 text-left text-sm">
              <tr>
                <th className="px-3 py-2">#</th>
                <th className="px-3 py-2">Full name</th>
                <th className="px-3 py-2">Qualification</th>
                <th className="px-3 py-2">Position</th>
                <th className="px-3 py-2">Phone number</th>
                <th className="px-3 py-2">Current district</th>
                <th className="px-3 py-2">Current school</th>
                <th className="px-3 py-2">Requested school</th>
                <th className="px-3 py-2">Accepted school</th>
                <th className="px-3 py-2">Status</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td colSpan={10}>
                  <input
                    onChange={(e) =>
                      this.setState({ searchData: e.target.value })
                    }
                    placeholder="Search by name"
                    className="border border-gray-400 px-3 py-2 rounded w-full my-1"
                  />
                </td>
              </tr>
              {search(this.state.data, this.state.searchData).length === 0 ? (
                <tr>
                  <td colSpan={10} className="">
                    <div className="bg-gray-100 rounded-md p-5 text-2xl font-light text-center">
                      No requests found!
                    </div>
                  </td>
                </tr>
              ) : (
                (
                  search(
                    this.state.data,
                    this.state.searchData
                  ) as TransferRequestsListInterface[]
                ).map((item, i) => (
                  <tr
                    key={i + 1}
                    className={`${
                      i % 2 !== 0 ? "bg-gray-100" : ""
                    } hover:bg-blue-50 group text-sm`}
                  >
                    <td className="px-3 py-2">{i + 1}</td>
                    <td className="px-3 py-2">{item.teacher_full_name}</td>
                    <td className="px-3 py-2">{item.qualification_name}</td>
                    <td className="px-3 py-2">{item.position_name}</td>
                    <td className="px-3 py-2">{item.teacher_phone_number}</td>
                    <td className="px-3 py-2">
                      {item.school_from_info?.district_name}
                    </td>
                    <td className="px-3 py-2">
                      {item.school_from_info?.school_name}
                    </td>
                    <td className="px-3 py-2">
                      {item.requested_school_info?.school_name}
                    </td>
                    <td>{item.approved_school_info?.school_name}</td>
                    <td>
                      {item.requested_status === TRANSFER_REQUEST_STATUS.PENDING
                        ? "Waiting"
                        : item.requested_status}
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </DashboardContainer>
    );
  }
}
